import { useContext } from 'react'
import {Link} from 'react-router-dom'
import {AuthContext} from '../context/auth.context'

export const Header = () => {
    const {isAuthenticatedAdmin, isAuthenticatedDoctor, isAuthenticatedPatient, userId, logoutHandler} = useContext(AuthContext)
    if (isAuthenticatedPatient) {
        return (
            <header>
                <nav>
                    <button><Link to="/"></Link></button>
                    <button><Link to={`/recomendations/${userId}`}></Link></button>
                    <button><Link to={`/directions/${userId}`}></Link></button>
                    <button><Link to="/about"> </Link></button>
                    <button onClick={ logoutHandler }><Link to="/"></Link></button>
                </nav>
            </header>
        )
    }
    if (isAuthenticatedDoctor) {
        return (
            <header>
                <nav>
                    <button><Link to="/"></Link></button>
                    <button><Link to="/about"> </Link></button>
                    <button onClick={ logoutHandler }><Link to="/"></Link></button>
                </nav>
            </header>
        )
    }
    if (isAuthenticatedAdmin) {
        return (
            <header>
                <nav>
                    <button><Link to="/"></Link></button>
                    <button><Link to="/about"> </Link></button>
                    <button onClick={ logoutHandler }><Link to="/"></Link></button>
                </nav>
            </header>
        )
    }
    return (
        <header>
            <nav>
                <button><Link to="/"></Link></button>
                <button><Link to="/login"></Link></button>
                <button><Link to="/registration"></Link></button>
                <button><Link to="/about"> </Link></button>
            </nav>
        </header>
    )
}
