import React from "react"
import {Header} from '../../components/Header'
import {Footer} from '../../components/Footer'
import { Link } from "react-router-dom"

//   

export const AdminPage = () => {
    return (
        <>
            <Header/>
            <main>
                <div className="adminContent">
                    <Link to='/patients'><button></button></Link>
                    <Link to='/doctors'><button></button></Link>
                    <Link to="/dicts"><button></button></Link>
                    <Link to="/original-questionnaire"><button> </button></Link>
                </div>
            </main>
            <Footer/>
        </>
    )
}