import React, { useEffect } from "react"
import { Footer } from "../components/Footer"
import { Header } from "../components/Header"
import { Message } from "../components/Message"
import { useMessage } from "../hooks/message.hook"

const crc32 =require('crc-32')



const htmlTrue = `981977849`

//   

export const AboutPage = () => {

    const {message, showMessage, hideMessage} = useMessage()

    useEffect(() => {
        if (message) {
            const inter = setInterval(() => {
                hideMessage()
                clearInterval(inter)
            }, 5000)
        }
    }, [message])

    const clickHandler = () => {
        const reqToPath = async() => {
            try {
                const res = await fetch('/about.html', {method:'GET', headers: {['Content-type']:'application/html'}})
                if (res.status === 404) {
                    showMessage('  ')
                } else {
                    const info = await res.text()
                    const hash = crc32.str(info)
                    // console.log(hash)
                    //,     htmlTrue
                    // if (String(hash) === htmlTrue) {
                        const link = document.createElement('a')
                        link.setAttribute('href', '/about.html')
                        link.setAttribute('target', 'blank')
                        link.click()
                    // } else {
                    //     showMessage('        ')
                    // }
                }
            } catch (e) {
                showMessage("    - "+e)
            }
        }
        reqToPath()
    }






    return (
        <>
            <Header/>
            <main>
                {message && <Message message={message}/>}
                <div className="instruction">
                    <span>  </span>
                    <p> </p>
                    <p>   </p>
                    <span>  </span>
                    <span>
                         : "       
                           "
                    </span>
                    <p></p>
                    <p> (  6415-020302D):</p>
                    <span> ..</span>
                    <span className="instruction-year">2023.</span>
                    <button onClick={clickHandler}>    </button>
                </div>
            </main>
            <Footer/>
        </>
    )
}